@import "../settings/variables";
@import "../settings/mixins";

.about {
    margin-top: 100px;

    &__title {
        font-family: $f-raleway;
        font-size: 2.25em;
        font-weight: 400;
        text-align: center;
        margin: 0;

        @include mobile {
            padding-bottom: 50px;
            margin: auto;
            width: 80%;
            transition: 0.2s;
        }
    }
}

.about__singles-container {
    display: flex;
    justify-content: space-around;
    margin: 100px auto 120px;
    max-width: 800px;

    @include mobile {
        margin: 35px auto 50px;
        align-items: center;
        flex-direction: column;
    }
}

.about__single {
    width: 163px;
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    align-items: center;
    transition: 0.2s;

    @include mobile {
        margin-bottom: 35px;

        &:last-child {
            margin-bottom: 0;
        }
    }

    &-title {
        font-family: $f-raleway;
        font-size: 1.25em;
        font-weight: 800;
        text-align: center;
        letter-spacing: 5.96px;
        margin-top: 14px;
        margin-bottom: 19px;
    }

    &-text {
        font-family: $f-raleway;
        color: $c-grey;
        font-size: 1em;
        font-weight: 400;
        text-align: center;
    }

    &-image {
        position: relative;
        bottom: 20px;
    }

    &:nth-child(1) .about__circle {
        background-color: $c-red;
    }
    &:nth-child(2) .about__circle {
        background-color: $c-blue;
    }
    &:nth-child(3) .about__circle {
        background-color: $c-green;
    }
}

.about__circle {
    border-radius: 50%;
    width: 63px;
    height: 63px;
    display: flex;
    justify-content: center;
    align-items: center;

    z-index: -99;
}

.about__icon {
    position: absolute;
    left: 30px;
    bottom: 10px;
}