body {
  margin: 0;
  font-family: "Open Sans", sans-serif;
  overflow-x: hidden;
}

main {
  background-color: #F7F7F7;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  font-family: "Raleway", sans-serif;
}

.container {
  width: 1352px;
  margin: 0 auto;
}
@media (max-width: 1352px) {
  .container {
    width: 100%;
    padding: 0 15px;
  }
}

.header {
  height: 70px;
  box-shadow: 0 3px 6px #00000029;
  background-color: #fff;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 999;
}
.header__container {
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.company-logo {
  color: #08ACE6;
  font-family: "Raleway", sans-serif;
  font-size: 2em;
  font-weight: 300;
  text-align: left;
  text-decoration: none;
}

.menu__list {
  display: flex;
  list-style-type: none;
}
.menu__list-link {
  color: #A5A5A5;
  font-size: 0.6875em;
  font-weight: 700;
  letter-spacing: 0.205em;
  padding: 28px 18px 22px;
  text-transform: uppercase;
  align-items: center;
  border-bottom: 4px solid transparent;
}
.menu__list-link:hover {
  color: black;
  border-color: #08ACE6;
  background: rgba(255, 255, 255, 0.22);
  transition: 0.3s;
}
.menu__list-el.is-active .menu__list-link {
  color: #000;
}

@media (max-width: 792px) {
  .menu__list {
    overflow: hidden;
    padding: 20px;
    transition: 0.2s;
    position: absolute;
    left: 0;
    top: 70px;
    width: 100%;
    box-shadow: 0 3px 6px #00000029, inset 2px 4px 6px #00000029;
    background-color: #fff;
    flex-direction: column;
    opacity: 0;
    pointer-events: none;
  }
  .menu__list-link {
    padding: 14px 20px;
  }
  .menu__list-link:hover {
    border: none;
  }

  .menu__list.show {
    opacity: 1;
    pointer-events: all;
    margin-top: 0;
  }
}
.dropdown-burger {
  display: none;
  cursor: pointer;
  border: none;
  outline: none;
  background-color: white;
}
.dropdown-burger .el1, .dropdown-burger .el2, .dropdown-burger .el3 {
  width: 35px;
  height: 5px;
  background-color: #08ACE6;
  margin: 6px 0;
  transition: 0.4s;
  border-radius: 5px;
}
.dropdown-burger.change .el1 {
  -webkit-transform: rotate(-45deg) translate(-9px, 6px);
          transform: rotate(-45deg) translate(-9px, 6px);
}
.dropdown-burger.change .el2 {
  opacity: 0;
}
.dropdown-burger.change .el3 {
  -webkit-transform: rotate(45deg) translate(-8px, -8px);
          transform: rotate(45deg) translate(-8px, -8px);
}
@media (max-width: 792px) {
  .dropdown-burger {
    display: inline-block;
    position: relative;
    overflow: hidden;
    transition: 0.5s;
  }
}

.banner {
  width: 100%;
  margin-top: 130px;
  display: flex;
  align-items: center;
  flex-direction: column;
  text-shadow: 0.5px 0.5px rgba(0, 0, 0, 0.24);
  position: relative;
}

.banner__title {
  color: black;
  font-family: "Raleway", sans-serif;
  font-size: 3.0625em;
  font-weight: 400;
  text-align: center;
  padding: 20px;
}

.banner__text {
  font-size: 16px;
  font-weight: 400;
  text-align: center;
  line-height: 22px;
  padding-bottom: 38px;
}

.banner__btn {
  padding: 22px 157px;
  background: #08ACE6;
  color: white;
  font-size: 14px;
  font-weight: 700;
  box-shadow: 5px 10px 14px #07ACE629;
  border-radius: 35px;
  text-decoration: none;
}
@media (max-width: 792px) {
  .banner__btn {
    transition: 0.2s;
    margin: 0 15px;
    padding: 20px 100px;
  }
}
@media (max-width: 400px) {
  .banner__btn {
    padding: 20px 50px;
  }
}

.banner__btn:hover {
  box-shadow: 0 0 15px 10px #07ACE629;
}

.banner__image {
  width: 100%;
  height: 594px;
  margin-bottom: 0;
  flex-grow: 1;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.banner__image-img {
  -o-object-fit: cover;
     object-fit: cover;
  bottom: 0;
  left: 0;
  right: 0;
}

.about {
  margin-top: 100px;
}
.about__title {
  font-family: "Raleway", sans-serif;
  font-size: 2.25em;
  font-weight: 400;
  text-align: center;
  margin: 0;
}
@media (max-width: 792px) {
  .about__title {
    padding-bottom: 50px;
    margin: auto;
    width: 80%;
    transition: 0.2s;
  }
}

.about__singles-container {
  display: flex;
  justify-content: space-around;
  margin: 100px auto 120px;
  max-width: 800px;
}
@media (max-width: 792px) {
  .about__singles-container {
    margin: 35px auto 50px;
    align-items: center;
    flex-direction: column;
  }
}

.about__single {
  width: 163px;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-items: center;
  transition: 0.2s;
}
@media (max-width: 792px) {
  .about__single {
    margin-bottom: 35px;
  }
  .about__single:last-child {
    margin-bottom: 0;
  }
}
.about__single-title {
  font-family: "Raleway", sans-serif;
  font-size: 1.25em;
  font-weight: 800;
  text-align: center;
  letter-spacing: 5.96px;
  margin-top: 14px;
  margin-bottom: 19px;
}
.about__single-text {
  font-family: "Raleway", sans-serif;
  color: #A5A5A5;
  font-size: 1em;
  font-weight: 400;
  text-align: center;
}
.about__single-image {
  position: relative;
  bottom: 20px;
}
.about__single:nth-child(1) .about__circle {
  background-color: #FB3B64;
}
.about__single:nth-child(2) .about__circle {
  background-color: #08ACE6;
}
.about__single:nth-child(3) .about__circle {
  background-color: #55DFB4;
}

.about__circle {
  border-radius: 50%;
  width: 63px;
  height: 63px;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: -99;
}

.about__icon {
  position: absolute;
  left: 30px;
  bottom: 10px;
}

.benefits {
  background-color: #F7F7F7;
  padding: 100px 0;
  text-shadow: 0.5px 0.5px rgba(0, 0, 0, 0.24);
}
@media (max-width: 1024px) {
  .benefits {
    padding: 50px 0;
  }
}
.benefits__container {
  padding: 0 100px;
}
@media (max-width: 1024px) {
  .benefits__container {
    padding: 0 15px;
  }
}
.benefits__row {
  background-color: white;
  border: 1px solid #E8E9ED;
  border-radius: 5px;
  margin-bottom: 37px;
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
}
@media (max-width: 1024px) {
  .benefits__row {
    flex-direction: column-reverse !important;
    align-items: center;
    padding: 0 20px !important;
  }
}
.benefits__row-content {
  box-sizing: content-box;
  max-width: 415px;
  margin: 90px 36px;
}
@media (max-width: 1024px) {
  .benefits__row-content {
    margin: 0;
    max-width: none;
  }
}
.benefits__row-content::after {
  content: "";
  width: 332px;
  height: 4px;
  background-color: var(--accent-color);
  display: block;
  margin-top: 50px;
}
@media (max-width: 1024px) {
  .benefits__row-content::after {
    display: none;
  }
}
.benefits__row-title {
  font-size: 2.25em;
  font-weight: 400;
}
@media (max-width: 1024px) {
  .benefits__row-title {
    text-align: center;
  }
}
.benefits__row-description {
  line-height: 1.375em;
  margin-top: 30px;
}
.benefits__row-image {
  position: relative;
}
.benefits__row img {
  position: relative;
  z-index: 2;
}
.benefits__double {
  --accent-color: #55DFB4;
  display: flex;
}
@media (max-width: 1024px) {
  .benefits__double {
    flex-direction: column;
  }
}
.benefits__double > div {
  margin-bottom: 0;
}
.benefits__double .benefits__row--solo {
  max-width: 620px;
}
.benefits__double .benefits__row--solo .benefits__row-content {
  max-width: none;
  margin: 70px 35px;
}
@media (max-width: 1024px) {
  .benefits__double .benefits__row--solo {
    max-width: none;
  }
  .benefits__double .benefits__row--solo .benefits__row-content {
    margin: 0;
    padding: 30px 0;
  }
}
.benefits__double .benefits__row-image--solo {
  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--accent-color);
  border: none;
  margin-left: 60px;
}
@media (max-width: 1024px) {
  .benefits__double .benefits__row-image--solo {
    margin-left: 0;
    margin-top: -5px;
  }
}
.benefits__double .benefits__row-image--solo img {
  width: 295px;
  height: auto;
  margin-top: 55px;
}

.benefits__row--macbook {
  --accent-color: #FB3B64;
}
.benefits__row--macbook .benefits__row-image {
  margin-right: 117px;
  margin-bottom: 38px;
}
@media (max-width: 1024px) {
  .benefits__row--macbook .benefits__row-image {
    margin: 40px 0 0;
  }
}
.benefits__row--macbook .benefits__row-image::before {
  content: "";
  width: 180px;
  height: 180px;
  background-color: var(--accent-color);
  display: block;
  border-radius: 40px;
  position: absolute;
  right: 0;
  top: -40px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
@media (max-width: 1024px) {
  .benefits__row--macbook .benefits__row-image::before {
    width: 90px;
    height: 90px;
    right: -10px;
    top: -30px;
  }
}
.benefits__row--macbook .benefits__row-image img {
  width: 386px;
  height: auto;
  position: relative;
  z-index: 2;
}
@media (max-width: 1024px) {
  .benefits__row--macbook .benefits__row-image img {
    width: 192px;
  }
}
@media (max-width: 1024px) {
  .benefits__row--macbook .benefits__row-content {
    margin: 30px 0;
  }
}
.benefits__row--iphone {
  --accent-color: #08ACE6;
  flex-direction: row-reverse;
  padding: 0 20px 0 70px;
}
@media (max-width: 1024px) {
  .benefits__row--iphone .benefits__row-image {
    margin-top: 30px;
  }
}
.benefits__row--iphone .benefits__row-image::before {
  content: "";
  width: 320px;
  height: 320px;
  background-color: var(--accent-color);
  display: block;
  border-radius: 50%;
  position: absolute;
  left: 0;
  top: -20px;
}
@media (max-width: 1024px) {
  .benefits__row--iphone .benefits__row-image::before {
    width: 131px;
    height: 131px;
    top: -15px;
  }
}
.benefits__row--iphone .benefits__row-image img {
  width: 426px;
  height: auto;
}
@media (max-width: 1024px) {
  .benefits__row--iphone .benefits__row-image img {
    width: 175px;
  }
}
.benefits__row--iphone .benefits__row-content {
  margin: 70px 0;
}
@media (max-width: 1024px) {
  .benefits__row--iphone .benefits__row-content {
    margin: 30px 0;
  }
}

